.overlay {
  position: fixed;  
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  top: 0;
  left: 0;
  backdrop-filter: blur(20px);
  z-index: 10;
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal {
  border-radius: .5rem;
  padding: 2rem;
  box-shadow: 3px 3px 20px rgba(0, 0, 0, 0.24);
  min-width: 20rem;
  background-color: var(--color-bg);
}

:global(.warning) h1 {
  color: var(--color-second)
}
